<template>
  <div class="inContainer">
    <div class="inbox">
      <div class="gys-list">
        <div class="layoutgr">
          <el-form>
            <el-form-item label=":">
              <el-input placeholder="/"></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-select v-model="value" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=" ">
              <el-select v-model="value2" placeholder="">
                <el-option
                  v-for="item in options2"
                  :key="item.value2"
                  :label="item.label2"
                  :value="item.value2"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-button type="primary" class="fa fa-search"></el-button>
            <el-button plain class="fa fa-undo"></el-button>
          </el-form>
        </div>
      </div>

      <div class="grid">
        <div class="layoutitem" style="width:100%;border:none;">
          <div>
            <span></span>
            <div class="fy2">
              <el-button plain class="fa fa-plus"></el-button>
              <el-button plain class="fa fa-pencil"></el-button>
              <el-button plain class="fa fa-trash-o"></el-button>
              <el-button plain class="fa fa-refresh"></el-button>
              <el-button plain class="fa fa-upload"></el-button>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            height="500"
            :data="IntableList"
            border
            style="width: 100%"
            :default-sort="{prop: 'ID', order: 'ascending'}"
          >
            <el-table-column type="selection"></el-table-column>
            <el-table-column prop="ID" label="" width="80" sortable></el-table-column>
            <el-table-column prop="number" label="" width="100" sortable></el-table-column>
            <el-table-column prop="spbh" label="" width="100" sortable></el-table-column>
            <el-table-column prop="name" label="" width="95" sortable></el-table-column>
            <el-table-column prop="ck" label="" width="60" sortable></el-table-column>
            <el-table-column prop="quantity" label="" width="60" sortable></el-table-column>
            <el-table-column prop="dw" label="" width="110" sortable></el-table-column>
            <el-table-column prop="rkdj" label="" width="100" sortable></el-table-column>
            <el-table-column prop="rkje" label="" width="110" sortable></el-table-column>
            <el-table-column prop="bz" label="" width="90" sortable></el-table-column>
            <el-table-column prop="bz" label="" width="100" sortable></el-table-column>
            <el-table-column prop="bz" label="" width="80"></el-table-column>
            <el-table-column prop="bz" label="" width="100"></el-table-column>
            <el-table-column label="" width="200" fixed="right">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleEdit(slot-scope.$index, scope.row)"
                  class="fa fa-arrow-circle-right"
                ></el-button>
                <el-button
                  size="mini"
                  @click="handleEdit(slot-scope.$index, scope.row)"
                  class="fa fa-pencil"
                ></el-button>
                <el-button
                  size="mini"
                  @click.native.prevent="handdeleteRow(slot-scope.$index, IntableList)"
                  class="fa fa-trash"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="di">
            <div>
              <el-checkbox v-model="qx1" @change="qx(IntableList)" label="" border></el-checkbox>
              <el-checkbox v-model="fx1" @change="fx(IntableList)" label="" border></el-checkbox>
            </div>
            <div>
              <el-select v-model="value" disabled placeholder=""></el-select>
            </div>
            <div class="fy">
              <el-pagination :page-sizes="[10, 20, 30, 40]" :page-size="50" :total="500"></el-pagination>
            </div>
          </div>
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogCreatVisible">
        <el-form
          class="small-space"
          :model="temp"
          label-position="right"
          label-width="70px"
          style="width: 400px; margin-left:50px;"
        >
          <el-form-item label="">
            <el-input v-model="temp.number"></el-input>
          </el-form-item>

          <el-form-item label="ID">
            <el-input v-model="temp.ID"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.spbh"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.dw"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.ck"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.quantity"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.rkdj"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.rkje"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.bz"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogCreatVisible = false"> </el-button>

          <el-button type="primary" @click="handleCreateSubmit"> </el-button>
        </div>
      </el-dialog>
      <el-dialog title="" :visible.sync="dialogEditVisible">
        <el-form
          class="small-space"
          :model="temp"
          label-position="right"
          label-width="70px"
          style="width: 400px; margin-left:50px;"
        >
          <el-form-item label="">
            <el-input v-model="temp.number"></el-input>
          </el-form-item>

          <el-form-item label="ID">
            <el-input v-model="temp.ID"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.spbh"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.dw"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="temp.ck"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.quantity"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.rkdj"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.rkje"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="temp.bz"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogEditVisible = false"> </el-button>

          <el-button type="primary" @click="handleEditSubmit"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { api } from "@/global/api";
import Vue from "vue";
export default {
  data() {
    return {
      qx1: false,
      fx1: false,
      options: [
        {
          value: "1",
          label: " "
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        }
      ],
      options2: [
        {
          value2: "1",
          label2: " "
        },
        {
          value2: "2",
          label2: ""
        },
        {
          value2: "3",
          label2: ""
        },
        {
          value2: "4",
          label2: ""
        }
      ],
      value2: "",
      value: "",
      value1: "",
      dialogCreatVisible: false,
      dialogEditVisible: false,
      isShow: false,
      checkList: [],
      IntableList: [],
      temp: {
        number: "",
        ID: 22,
        spbh: "",
        name: "",
        dw: "",
        ck: "",
        quantity: "",
        rkdj: "",
        rkje: "",
        bz: ""
      }
    };
  },
  mounted() {
    var vm = this;
    vm.getData();
  },
  methods: {
    getData() {
      this.$http.get(api.kfData).then(
        function(response) {
          console.log(response);
          console.log("json", response.data.IntableData);
          this.IntableList = response.data.IntableData;
        },
        function(response) {
          alert("");
        }
      );
    },
    handleCreate() {
      this.dialogCreatVisible = true;
    },
    handleEdit() {
      this.dialogEditVisible = true;
    },
    qx() {
      //
      this.fx1 = false;
      if (this.$refs.multipleTable.selection.length < this.IntableList.length) {
        this.qx1 = true;
      } else {
        this.qx1 = false;
      }
      if (this.qx1) {
        this.IntableList.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row, true);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectedAssetsList = [];
        this.assetsUidList = [];
      }
    },
    fx(rows) {
      //
      this.qx1 = false;
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    //
    handleCreateSubmit() {
      var vm = this;
      var flag = 0;
      for (var intablelist of vm.IntableList) {
        console.log("flag", flag);
        if (vm.temp.ID != intablelist.ID) {
          console.log("ID", intablelist.ID);
          flag++;
        }
      }
      console.log("ifflag", flag);
      if (flag == vm.IntableList.length) {
        vm.IntableList.push(vm.temp);
        vm.temp = JSON.parse(JSON.stringify(vm.temp));
      } else {
        alert("ID,");
      }
      this.dialogCreatVisible = false;
    },
    //
    handleEditSubmit() {
      var vm = this;
      var flag = 0;
      for (var intablelist of vm.IntableList) {
        if (vm.temp.ID == intablelist.ID) {
          var index = vm.IntableList.indexOf(intablelist);
          console.log("", index);
          Vue.set(vm.IntableList, index, vm.temp);
          vm.temp = JSON.parse(JSON.stringify(vm.temp));
          flag++;
        }
      }
      if (flag == 0) {
        alert("ID");
      }
      this.dialogEditVisible = false;
    },
    // 
    handdeleteRow(index, rows) {
      rows.splice(index, 1);
    }
  }
};
</script>
<style>
.fy2 {
  float: right;
  margin-bottom: 1%;
}
.fy {
  float: right;
}
.di > div {
  margin-top: 1%;
  display: inline-block;
}
.inContainer {
  float: left;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.inbox {
  padding: 20px 24px;
}
.con-header {
  width: 100%;
  overflow: hidden;
}
.header-rg {
  float: right;
  padding-right: 25px;
  display: block;
}
.inbox .el-form-item {
  float: left;
  padding-right: 8px;
}
.inbox .el-form-item .el-form-item__content {
  float: left;
}
.header-rg .el-input__inner {
  height: 30px;
  width: 120px;
}
.layoutgr {
  padding-left: 20px;
}
.inbox .gys-list .layoutgr .el-form-item {
  float: left;
  padding-right: 40px;
  padding-bottom: 5px;
  margin-bottom: 0;
}
.layoutgr {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  overflow: hidden;
}
.inbox .gys-list .layoutgr .el-form-item__label {
  width: inherit;
}
.layoutgr .el-input__inner {
  height: 30px;
  width: 160px;
}
.cellspacing {
  margin-top: 10px;
}
.cell-zd {
  float: left;
  display: inline-block;
}
.cell-je {
  float: right;
  padding-right: 12px;
}
.small-space .el-form-item {
  margin-bottom: 10px;
}
.cell-jespan {
  color: white;
}
.inbox .el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: white;
  background-clip: padding-box;
}
.inbox .el-input__icon {
  right: 70px;
}
.inbox .el-dialog__footer {
  clear: both;
}
.el-table .cell,
.el-table th > div {
  padding-left: 5px;
  padding-right: 5px;
}
</style>


