<template>
  <div class="customerOrder">
    <div class="cum_header">
      <span style="font-size: 20px;"></span>
      <span class="Csearch">
        <!-- <el-input v-model="formInline.cum" placeholder=""></el-input> -->
        <!--    <el-button type="primary" @click="handleSearch"></el-button> -->
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label>
            <el-input v-model="formInline.cum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch"></el-button>
          </el-form-item>
        </el-form>
      </span>
    </div>
    <!--  -->
    <el-table
      ref="multipleTable"
      :data="tableData3"
      border
      tooltip-effect="dark"
      style="width: 100%;margin-bottom: 19px;"
      @selection-change
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="id" label="" width="70"></el-table-column>
      <el-table-column prop="name" label="" width="120"></el-table-column>
      <el-table-column prop="phone" label="" width="140"></el-table-column>
      <el-table-column prop="number" label="" width="100"></el-table-column>
      <el-table-column prop="total" label="" width="120"></el-table-column>
      <el-table-column prop="payS_shipS" label="/" width="170"></el-table-column>
      <el-table-column prop="status" label="" width="120"></el-table-column>
      <el-table-column prop="date" label="" width="120">
        <!--  -->
        <template scope="scope">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="small"
            type="danger"
            @click.native.prevent="handdeleteRow(scope.$index, tableData3)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-button style="background:#dfe6ec;" @click="handleCreate"></el-button>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogeditFormVisible">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.id" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.total" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="/" :label-width="formLabelWidth">
          <el-input v-model="form.payS_shipS" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.status" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.date"
            type="date"
            placeholder=""
            @change="dateChange"
            :picker-options="pickerOptions0"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="handleCreateOrder"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="editForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.id" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.total" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="/" :label-width="formLabelWidth">
          <el-input v-model="editForm.payS_shipS" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.status" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <!-- <el-input v-model="form.date" auto-complete="off"></el-input> -->
          <el-date-picker
            v-model="editForm.date"
            type="date"
            placeholder=""
            @change="dateChange"
            :picker-options="pickerOptions0"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="handledoneEdit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { api } from "@/global/api";
export default {
  data() {
    return {
      input: "",
      tableData3: [],
      multipleSelection: [],
      formInline: {
        cum: ""
      },

      dialogFormVisible: false,
      dialogeditFormVisible: false,
      form: {
        id: "",
        name: "",
        phone: "",
        number: "",
        total: "",
        payS_shipS: "",
        status: "",
        date: "",
        operate: "",
        delivery: false
      },
      editForm: {
        id: "",
        name: "",
        phone: "",
        number: "",
        total: "",
        payS_shipS: "",
        status: "",
        date: "",
        operate: "",
        delivery: false
      },
      formLabelWidth: "140px",
      pickerOptions0: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      }
    };
  },
  mounted() {
    var vm = this;
    vm.getList();
  },
  methods: {
    //json
    getList: function() {
      var vm = this;
      this.$http.get(api.customerOrder).then(function(response) {
        console.log(response);
        vm.tableData3 = response.data.tableData3;
      });
    },

    //
    handleSearch() {
      console.log("1111");
      var vm = this;
      for (var i = 0; i < vm.tableData3.length + 1; i++) {
        //
        for (var item of vm.tableData3) {
          //
          if (vm.formInline.cum != item.id) {
            var index = vm.tableData3.indexOf(item);
            console.log(index);
            vm.tableData3.splice(index, 1); //indexidindex
          }
        }
      }
    },
    //json
    // toggleSelection(rows) {
    //   if (rows) {
    //     rows.forEach(row => {
    //       this.$refs.multipleTable.toggleRowSelection(row);
    //     });
    //   } else {
    //     this.$refs.multipleTable.clearSelection();
    //   }
    // },
    // handleSelectionChange(val) {
    //   this.multipleSelection = val;
    // },
    // 
    handdeleteRow(index, rows) {
      rows.splice(index, 1);
    },
    //
    initForm() {
      let vm = this;
      vm.form = {
        id: "",
        name: "",
        phone: "",
        number: "",
        total: "",
        payS_shipS: "",
        status: "",
        date: "",
        operate: "",
        delivery: false
      };
    },
    // 
    handleCreate() {
      //
      this.initForm();
      this.dialogeditFormVisible = true;
    },
    //
    handleCreateOrder() {
      var vm = this;
      console.log(":", vm.form);
      vm.tableData3.push(vm.form);
      console.log(":", vm.tableData3);
      this.dialogeditFormVisible = false;
      // 
    },
    // 
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.editForm = row; //
      this.editIndex = index;
    },
    //
    handledoneEdit() {
      console.log(this.editIndex);
      console.log(this.editForm);
      var vm = this;
      vm.tableData3[this.editIndex] = this.editForm;
      this.dialogFormVisible = false; //false
      // 
      this.$message({
        showClose: true,
        message: "",
        type: "success"
      });
    },

    //
    dateChange(val) {
      this.form.date = val;
    }
  }
};
</script>

<style>
.customerOrder {
  position: absolute;
  width: 1178px;
}
.customerOrder .cum_header {
  font-size: 20px;
  color: #fff;
  background: #13ce66;
  line-height: 39px;
  padding: 9px 25px;
}
.customerOrder .Csearch {
  display: inline-block;
  margin-left: 674px;
}
.customerOrder .Csearch.el-input,
.customerOrder .Csearch .el-input__inner {
  width: 258px;
}
.customerOrder .el-date-editor.el-input {
  width: 253px;
}
</style>


